import axios from "axios";

export async function getAllTasks() {

  let res = await axios.get('https://1l4sfqk1wh.execute-api.us-east-1.amazonaws.com/default/getAllTasks');
  return res.data;

  // return [{"task_id":"2","task":"Haircut", "isComplete": true},{"task_id":"1","task":"Finish Lambda basics", "isComplete": true}];

}

export async function createTask(task, isComplete){
  let res = await axios.post('https://18tcsnkaef.execute-api.us-east-1.amazonaws.com/diary-prod/tasks', { task, isComplete });
  return res.data;
}

export async function updateTasks(tasks){
  let res = await axios.post('https://18tcsnkaef.execute-api.us-east-1.amazonaws.com/diary-prod/updateTasks', { tasks });
  return res.data;
  // return { message :'Updated', tasks: tasks };
}

export async function deleteTasks(tasks){
  let res = await axios.post('https://18tcsnkaef.execute-api.us-east-1.amazonaws.com/diary-prod/deleteTask', { tasks });
  return res.data;
  // return { message :'Updated', tasks: tasks };
}