import Checkbox from "../../CustomComponents/Checkbox/Checkbox";
import styles from "./Tasks.module.css";
import { useState, useRef } from "react";
import React from "react";

import { Pages } from "../../Constants";

import AddButton from "../../CustomComponents/AddButtom/AddButton";

import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import Spinner from "react-bootstrap/Spinner";
import Toast from "react-bootstrap/Toast";
import ToastContainer from "react-bootstrap/ToastContainer";

import { createTask, updateTasks, deleteTasks } from "../../Services/WebService";

function Tasks(props) {
  const [show, setShow] = useState(false);
  const [showToast, setShowToast] = useState(false);
  const [showSaveBtn, setShowSaveBtn] = useState(false);
  const [toastText, setToastText] = useState("");
  const [newTask, setNewTask] = useState("");
  const [loading, setLoading] = useState(false);
  const [deleteMode, setDeleteMode] = useState(false);
  const [tasks, setTasks] = useState(JSON.parse(JSON.stringify(props.tasks)))
  const form = useRef();
  const tTasks = useRef(JSON.parse(JSON.stringify(props.tasks)));
  const updatedList = useRef([]);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  async function handleSubmit() {
    setLoading(true);
    let res = await createTask(newTask, false);
    tTasks.current = [...tTasks.current, res.task];
    setTasks(tTasks.current);
    handleClose();
    setLoading(false)
    setShowToast(true);
    setToastText(res.message);
  }

  function onChange(e) {
    setNewTask(e.target.value);
  }

  async function saveChanges() {
    setLoading(true);
    if(deleteMode){
      let res = await deleteTasks(updatedList.current);  
      setTasks(tTasks.current);
      setToastText(res.message);
    }else{
      let res = await updateTasks(updatedList.current);
      tTasks.current.forEach(task => {
        res.tasks.forEach(updatedTask => {
          if(tTasks.task_id === updatedTask.task_id){
            task.current.isComplete = updatedTask.isComplete;
          }
        })
      });
      setTasks(tTasks.current)
      setToastText(res.message);
    }
    updatedList.current = [];
    setLoading(false);
    setShowToast(true);
    setShowSaveBtn(false);
    setDeleteMode(false);
  }

  function handleDeleteItem(e, task) {
    e.target.classList.add('text-danger');
    setShowSaveBtn(true);
    updatedList.current.push(task);
    let newList = tTasks.current.filter(t => {
      return t.task_id !== task.task_id;
    })
    tTasks.current = newList;
  }

  return (
    <>
      <div
        className="d-flex position-sticky p-3 top-0 bg-light"
        style={{ "zIndex": 99 }}
      >
        <Button
          onClick={() => props.navigateTo(Pages.HOME)}
          variant="btn btn-outline-primary bg-white"
        >
          <i className="fa fa-angle-left"></i>Back
        </Button>
        <Button
          type="button"
          variant="btn btn-btn btn-danger ms-auto"
          onClick={() => {
            if(deleteMode){
              tTasks.current = props.tasks;
            }
            setDeleteMode(!deleteMode);
          }}
        >
          {deleteMode ? (
            "Cancel"
          ) : (
            <i className="fa fa-trash color-white" aria-hidden="true"></i>
          )}
        </Button>
      </div>
      <div className={styles.tasksContainer}>
        {tasks?.map((task, index) => {
          return (
            <div
              className={`d-flex ${styles.task}`}
              key={task.task_id}
            >
              <Checkbox
                disabled={loading}
                tasks={tTasks.current}
                index={index}
                isComplete={task.isComplete}
                name={task.task}
                callback={() => {
                  let isEqual = true;
                  updatedList.current = [];
                  tasks.forEach((task) => {
                    tTasks.current.forEach((tTask) => {
                      if (
                        task.task_id === tTask.task_id &&
                        task.isComplete !== tTask.isComplete
                      ) {
                        isEqual = false;
                        updatedList.current.push(task);
                        setShowSaveBtn(true);
                      }
                    });
                  });
                  if (isEqual) {
                    setShowSaveBtn(false);
                  }
                }}
              />
              {deleteMode && (
                <Button
                  variant="btn btn-outline-info"
                  className={styles.trashBtn}
                  onClick={(e) => { handleDeleteItem(e, task) }}
                >
                  <i class="fa fa-trash" aria-hidden="true"></i>
                </Button>
              )}
            </div>
          );
        })}

        <ToastContainer className="p-3" position="bottom-center">
          <Toast
            onClose={() => setShowToast(false)}
            delay={3000}
            show={showToast}
            autohide
          >
            <Toast.Header closeButton={false}>
              <img
                src="holder.js/20x20?text=%20"
                className="rounded me-2"
                alt=""
              />
              <strong className="me-auto">Task</strong>
            </Toast.Header>
            <Toast.Body>{toastText}</Toast.Body>
          </Toast>
        </ToastContainer>
      </div>
      <Modal show={show} onHide={handleClose} centered>
        <Modal.Header closeButton>
          <Modal.Title>Create Task</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form ref={form}>
            <Form.Group className="mb-3" controlId="newTask">
              <Form.Label>Task</Form.Label>
              <Form.Control type="text" autoFocus onChange={onChange} />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleSubmit} disabled={loading}>
            {loading && (
              <Spinner
                className="me-2"
                as="span"
                animation="border"
                size="sm"
                role="status"
                aria-hidden="true"
              />
            )}
            Save
          </Button>
        </Modal.Footer>
      </Modal>
      {showSaveBtn && (
        <Button
          variant="primary"
          className={`centered ${styles.saveBtn} btn-lg`}
          disabled={loading}
          onClick={saveChanges}
        >
          {loading && (
            <Spinner
              className="me-2"
              as="span"
              animation="border"
              size="sm"
              role="status"
              aria-hidden="true"
            />
          )}
          Save
        </Button>
      )}
      {!deleteMode && <AddButton invokeFunction={handleShow} />}
    </>
  );
}

export default Tasks;
